.info_panel {
  width: 100%;
  height: 100%;
  padding: 10px;
  background-color: #f5f5f5;
}
.info_panel .info_panel_tabs_leftInfo {
  width: 180px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.info_panel .info_panel_tabs_icon {
  margin-right: 6px;
  font-size: 12px;
}
.info_panel .info_panel_tabs_span {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
}
.info_panel .info_layout {
  background-color: #fff;
  padding: 12px 20px;
  height: 100%;
  overflow-y: scroll;
}
.info_panel .info_layout .info_layout_header {
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 16px;
}
.info_panel .info_layout .info_layout_header .info_layout_header_title {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 600;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.info_panel .info_layout .info_layout_header .info_layout_header_content {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #666666;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  word-wrap: break-word;
  margin-top: 8px;
}
.info_panel .info_layout .info_layout_content {
  padding-top: 13px;
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li {
  background-color: #f5f5f5;
  width: 45%;
  padding: 12px 16px;
  flex-grow: 1;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_title {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 600;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_text {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #666666;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  padding: 8px 0px;
  border-bottom: 1px solid #d9d9d9;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_text .info_layout_content_li_text_info {
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  height: 32px;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_kg {
  padding: 7px 0px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_kg .info_layout_content_li_kg_icon {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #666666;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  flex-shrink: 0;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_kg .info_layout_content_li_kg_icon .info_layout_content_li_kg_icon_tt {
  font-size: 9px;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_kg .info_layout_content_li_kg_item {
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li .info_layout_content_li_kg .info_layout_content_li_kg_item .info_layout_content_li_kg_item_li {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: var(--theme-color);
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  cursor: pointer;
  margin-right: 20px;
  max-width: 150px;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.info_panel .info_layout .info_layout_content .info_layout_content_li_blank {
  background-color: #fff;
}
.info_layout_content_li_kg_item_li_pop {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: var(--theme-color);
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  cursor: pointer;
}
